import '../../css/Register.css'
import { React, useState} from "react";
import {useNavigate} from 'react-router-dom';
import Axios from "axios";

function Register() {
    // useState
    //        

    //  
    const [userName, setUserid] = useState('');
    const [password, setPassword] = useState('');
    const [pwConfirm, setPwConfirm] = useState('');
    const [registerStatus, setRegisterStatus] = useState('');

    //   
    const [idMessage, setIdMessage] = useState("");
    const [pwMessage, setPwMsg] = useState("");
    const [pwConfirmMsg, setPwConfirmMsg] =useState("");

    // 
    const onChangeId = (e) => {
        const userName = e.target.value; //    
        setUserid(userName); // useState 
        const idRegExp = /^[a-zA-z0-9]{6,14}$/;
        if (!idRegExp.test(userName)) {
            setIdMessage("6-14     !");
        } else {
            //, db  
            setIdMessage("  .");
            //
            setIdMessage("  .");
        }
    };

    const onChangePw = (e) => {
        const password = e.target.value;
        setPassword(password);
        const pwRegExp =
          /^(?=.*[a-zA-Z])(?=.*[0-9]).{6,20}$/;
        if (!pwRegExp.test(password)) {
          setPwMsg(
            "+  6  !"
          );
        } else {
          setPwMsg(" ");
        }
      };

    const onChangePwConfirm = (e) => {
      const pwConfirm = e.target.value;
      setPwConfirm(pwConfirm);
      if (password !== pwConfirm) {
        setPwConfirmMsg("  .");
      } else {
        setPwConfirmMsg(" ");
      }
    };

    const navigate = useNavigate();
    //   
    const register = (e) => {
        e.preventDefault();
        // api   
        Axios.post("https://api-fvwt.onrender.com/api/auth/register",         
        {
            RegisterId: userName,
            RegisterPW: password,
        })
        // response 
        .then((response)=> {
            //   
            if(response.data.message){
                setRegisterStatus(response.data.message);
            }else {
                setRegisterStatus("")
                navigate("/"); //  ,,        
               
            }
        })
    }

    return(
        <div id="register">
            <div className="registerForm">
                <form>
                    <h4></h4>
                    <div id="idWrap">
                        <input 
                            className="textInput" 
                            type="text" 
                            name="userId" 
                            onChange={onChangeId}
                            placeholder=" 6-14  " required />
                        <span className="message"> {idMessage} </span>
                    </div>
                    
                    <div id="pwWrap">
                    <input 
                        className="textInput" 
                        type="password" 
                        name="password" 
                        onChange={onChangePw} 
                        placeholder=" 6-20     " required/> 
                        <span className="message"> {pwMessage} </span>
                    </div>
                    
                    <div id="pwCfWrap">
                    <div id="pwCfTitle"> <span> *</span></div>
                    <input 
                        className="textInput" 
                        type="password" 
                        name="pwConfirm" 
                        value={pwConfirm}
                        onChange={onChangePwConfirm} 
                        placeholder="   ." required/>
                        <span className="message"> {pwConfirmMsg} </span>
                    </div>
                    
                    <div id="Btn">
                        <div id="cancelBtn" onClick={() => navigate("/")}>
                            
                        </div>
                        <div 
                            id="registerBtn" 
                            type="submit" 
                            onClick={register} ></div>
                    </div>

                </form>
            </div>
        </div>
    )
}

export default Register;