import React,{useState} from 'react';
import daegu from '../../images/daegu.svg'
import logo from '../../images/dandi-logo2.png'
import '../../css/Button.css'
  
const FloatingButton = () => {
    const [modalDandi, setModalDandi] = useState(false);
    const toggleModalDandi = () => {
        setModalDandi(!modalDandi)
    }    

    const [modalInfo, setModalInfo] = useState(false);
    const toggleModalInfo = () => {
        setModalInfo(!modalInfo)
    }   

const [isOpen, setMenu] = useState(false);  //   false 

const toggleMenu = () => {
      setMenu(isOpen => !isOpen); // on,off  boolean
  }

  return(
      <div className="floatingBtn">
        <ul className={isOpen ? "show-menu" : "hide-menu"}> 
            <li ><div className='list' onClick={toggleModalDandi}>Dandi?</div></li>
            <li ><div className='list'  onClick={toggleModalInfo}></div></li>
        </ul>
            <img id="fabPic" src={daegu} onClick={()=>toggleMenu()}/>      


             {modalDandi && (
            <div className="homeModal" >
                <div className="homeModalOverlay"></div>
                <div className="modal-content-dandi">     
                    <div id="homeModalDandiTitle" ><img  src={logo}></img></div>
                    <p className="homeModalTxt"> ', '  <br/>
                       ! .</p>
                    <p className="homeModalTxt">   'Dandi' <br/>
                        !</p>
                    <p className="homeModalTxt">    ,<br/>
                       !</p>
                    <div className='closemodal' onClick={toggleModalDandi}></div>
                </div>
                </div>

            )}       

            {modalInfo && (
            <div className="homeModal" >
                <div className="homeModalOverlay"></div>
                <div className="modal-content-dandi">    
                    <h2 id="homeModalInfoTitle"></h2> 
                    <p className="homeModalTxt">Dandi     <br/>
                    QR  !</p>
                    <p className="homeModalTxt">QR      <br/>
                     !</p>
                    <p className="homeModalTxt">  ,  <br/>
                        <br/>
                     !</p>
                    <div className='closemodal' id="closeModal2" onClick={toggleModalInfo}></div>
                </div>
                </div>

            )}               
      </div>




  )

}

export default FloatingButton;